.App {
  text-align: center;
}

.clear {
  clear:both;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.tools-item {
  list-style-type: none;
  background: #eee;
  border-left: 3px solid #00af89;
  border-right: 20px solid white;
  margin-bottom: 20px;
  overflow: hidden;
}

.tools-item h4 {
  font-size: 16px;
  font-weight: normal;
  color: #666;
  line-height: 150%;
}

.tools-item h5 {
  font-weight: normal;
  font-style: italic;
}